
.container {
    overflow: hidden;
}
.row {
    display: flex;
    justify-content: center;
    margin-top: 100px;
}

.form-horizontal{
    background: #fff;
    padding-bottom: 80px;
    border-radius: 15px;
    text-align: center;
    margin: 0 auto;
    
}

.form-horizontal .heading{
    display: block;
    font-size: 35px;
    font-weight: 700;
    padding: 35px 0;
    border-bottom: 1px solid #f0f0f0;
    margin-bottom: 30px;
   }
   .form-horizontal .form-group{
    padding: 0 40px;
    margin: 0 0 25px 0;
    position: relative;
   }
   .form-horizontal .form-control{
    background: #f0f0f0;
    border: none;
    border-radius: 20px;
    box-shadow: none;
    padding: 0 20px 0 45px;
    height: 40px;
    transition: all 0.3s ease 0s;
   }
   .form-horizontal .form-control:focus{
    background: #e0e0e0;
    box-shadow: none;
    outline: 0 none;
   }
   .form-horizontal .form-group svg{
    position: absolute;
    top:12px;
    left: 60px;
    font-size: 17px;
    color: #c8c8c8;
    transition : all 0.5s ease 0s;
   }
   .form-horizontal .form-control:focus + svg{
    color: #00b4ef;
   }
   .form-horizontal .main-checkbox{
    float: left;
    width: 20px;
    height: 20px;
    background: #11a3fc;
    border-radius: 50%;
    position: relative;
    margin: 5px 0 0 5px;
    border: 1px solid #11a3fc;
   }
   
   
   
   .form-horizontal .text{
    float: left;
    margin-left: 7px;
    line-height: 20px;
    padding-top: 5px;
    text-transform: capitalize;
   }
   .form-horizontal .btn{
    float: right;
    font-size: 14px;
    color: #fff;
    background: #00b4ef;
    border-radius: 30px;
    padding: 10px 25px;
    border: none;
    text-transform: capitalize;
    transition: all 0.5s ease 0s;
    margin-bottom: 20px;
   }
   @media only screen and (max-width: 479px){
    .form-horizontal .form-group{
    padding: 0 25px;
    }
    .form-horizontal .form-group i{
    left: 45px;
    }
    .form-horizontal .btn{
    padding: 10px 20px;
    }
   }